import React from 'react';
import { connect, useDispatch } from 'react-redux';
import styled from 'styled-components';

import { PageLayout } from '../layout/PageLayout';
import SEOMetadata from '../utils/browser/SEOMetadata';
import { IPolaState } from '../state/types';
import { LoadBrowserLocation, SelectActivePage } from '../state/app/app-actions';
import { PageType, urls } from '../domain/website';
import { PageSection } from '../layout/PageSection';
import { Text, TitleSection } from '../styles/GlobalStyle.css';
import { ColumnsLayout, ContentColumn } from '../layout/ColumnsLayout';
import { PartnerService } from '../domain/partners/partners-service';
import { PartnersList } from '../components/partners/PartnersList';
import { padding } from '../styles/theme';
import { ResponsiveImage } from '../components/images/ResponsiveImage';
import Faq from '../components/Faq';
import { ImageContainer } from '../layout/ImageContainer';

interface IAboutPage {
  location?: Location;
}

const AboutPage = (props: IAboutPage) => {
  const { location } = props;
  const dispatch = useDispatch();

  React.useEffect(() => {
    if (location) {
      dispatch(LoadBrowserLocation(location));
      dispatch(SelectActivePage(PageType.ABOUT));
    }
  }, []);

  return (
    <PageLayout styles={{ marginTop: padding.big }}>
      <SEOMetadata pageTitle="O Poli" />
      <ColumnsLayout>
        <ContentColumn fraction={60}>
          <PageSection>
            <TitleSection>O Poli</TitleSection>
            <Text>
              Masz do maswki globalnych koncernw? Szukasz lokalnych firm tworzcych unikatowe produkty? Pola pomoe
              Ci odnale polskie wyroby. Zabierajc Pol na zakupy, odnajdujesz produkty z dusz i wspierasz polsk
              gospodark.
            </Text>
            <Text>
              Zeskanuj kod kreskowy z dowolnego produktu i dowiedz si wicej o firmie, ktra go wyprodukowaa. Pola
              powie Ci, czy dany producent opiera si na polskim kapitale, ma u nas swoj produkcj, tworzy
              wykwalifikowane miejsca pracy, jest czci zagranicznego koncernu.
            </Text>
            <Text>
              Jeli znajdziesz firm, ktrej nie ma w naszej bazie, koniecznie zgo j do nas. Pomoesz nam w ten
              sposb uzupenia unikatow baz polskich producentw.
            </Text>
          </PageSection>
          <PageSection>
            <TitleSection>Algorytm</TitleSection>
            <Text>
              Kademu producentowi Pola przypisuje od 0 do 100 punktw. Pierwsze 35 punktw przyznaje proporcjonalnie do
              udziau polskiego kapitau w konkretnym przedsibiorstwie. Dalsze 10 punktw otrzymuje ta firma, ktra
              jest zarejestrowana w Polsce, a kolejne 30, o ile produkuje w naszym kraju. Dalsze 15 punktw zaley od
              tego, czy zatrudnia w naszym kraju w obszarze bada i rozwoju. Wreszcie ostatnie 10 punktw otrzymuj
              firmy, ktre nie s czci zagranicznych koncernw.
            </Text>
            <Text>
              Liczba punktw zwizualizowana jest przy pomocy czerwonego paska. Dokadamy wszelkich stara aby dane w
              aplikacji zawsze odpowiaday rzeczywistoci i byy aktualizowane na bieco. Prosimy o zgaszanie
              wszelkich uwag i niejasnoci.
            </Text>
          </PageSection>
          <PageSection>
            <TitleSection>Filozofia dziaania</TitleSection>
            <Text>
              Staramy si by maksymalnie przejrzyci w naszych dziaaniach. Cao kodu rdowego serwisu udostpniamy
              na zasadach otwartego oprogramowania na{' '}
              <a href={urls.external.polaGitHub.href} target="__blank">
                koncie Klubu Jagielloskiego
              </a>{' '}
              w serwisie GitHub. Wktrce planujemy udostpni w Internecie cao bazy danych producentw wraz z
              histori zmian i rdami, na podstawie ktrych podejmujemy decyzj o liczbie punktw, ktre im
              przyznajemy. Dziaamy zgodnie z nasz{' '}
              <a href={urls.external.polaPrivacyPolicy.href} target="__blank">
                polityk prywatnoci
              </a>
              .
            </Text>
          </PageSection>
          <PageSection>
            <TitleSection>Partnerzy</TitleSection>
            <PartnersList partners={PartnerService.getAll()} />
          </PageSection>
          <Faq />
        </ContentColumn>
        <ContentColumn hideOnMobile={true} fraction={40}>
          <ImageContainer>
            <ResponsiveImage imageSrc="sok.png" />
          </ImageContainer>
        </ContentColumn>
      </ColumnsLayout>
    </PageLayout>
  );
};

export default connect((state: IPolaState) => ({}))(AboutPage);
